.Modal {
  background-color: #141414;
  justify-content: center;
  align-items: center;
  z-index: 99999999999999;
  position: absolute;
  top: 15%;
  left: 12%;
  right: 12%;
  border-radius: 7px;
  box-shadow: 4px 3px 5px 1px #444;
  padding: 30px;
  height: fit-content;
  display: flex;
  justify-content: space-between;
}

.modal-img {
  height: 400px;
  border-radius: 10px;
}

.modal-info {
  padding-left: 30px;
}

.modal-review {
  line-height: 1.8;
  font-size: 15px;
}

.info-divider {
  margin: 0px 15px;
}

.close-btn {
  cursor: pointer;
}

.modal-trailer {
  background-color: #b98c53;
  padding: 10px 30px;
  border-radius: 5px;
  color: whitesmoke;
  text-decoration: none;
  transition: 500ms;
}

.modal-trailer:hover {
  background-color: peru;
  transition: 500ms;
}

.modal-actors-list,
.modal-meta-info-trivia {
  font-weight: 100;
  font-size: 14px;
  opacity: 70%;
}

.modal-heading {
  align-items: flex-start;
  display: flex;
  justify-content: space-between;
}

.modal-meta-info-trivia,
.modal-meta-info,
.modal-extra-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 600px) {
  .Modal,
  .modal-info,
  .modal-meta-info {
    display: inline;
  }

  .Modal {
    top: 1.5%;
    left: 1.5%;
    right: 1.5%;
    padding: 15px;
  }

  .modal-img {
    width: 100%;
    height: auto;
    border-radius: 7px;
  }

  .modal-meta-info-trivia-mobile {
    line-height: 1.5;
    opacity: 70%;
  }

  .modal-review,
  .modal-extra-info,
  .modal-actors-list,
  .modal-meta-info-trivia-mobile {
    font-size: 12px;
  }

  .modal-review {
    font-size: 13px;
  }
  .info-divider {
    margin: 0px 10px;
  }
}
