.collection-parent-container {
  display: flex;
  flex-wrap: wrap;
}

.flip-card-container {
  height: 250px;
  width: 430px;
  background-color: transparent;
  border-radius: 10px;
}

.flip-card-container:hover {
  cursor: pointer;
}

.flip-front {
  height: inherit;
  width: inherit;
  border-radius: 10px;
}

.flip-back {
  line-height: 2;
  background-color: #141414;
  padding: 20px;
  border-radius: 10px;
  height: inherit;
  width: inherit;
}

.flip-back-heading {
  font-size: 30px;
  font-weight: bold;
}

.card-img-mob {
  height: auto;
  width: 85vw;
  border-radius: 5px;
  margin: 5px 0px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
